@import '../../variables/variables';
@import '../../variables/font';


.profile {
    &__menu {

        &-profile {
            display: flex;
            align-items: center;
            padding: 15px;
            margin-bottom: 30px;

            &-title {
                margin-left: 30px;
                caret-color: transparent;
                cursor: default;
            }
        }
    }
}

#imageName {
    position: absolute;
    color: white;
    top: 100px;
    font-size: 12px;
    caret-color: transparent;
}

.photo-input {
    position: relative;

    width: 148px;
    height: 148px;

    background: #EFEFEF;
    background-image: url('../../image/profile.svg');
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;

    overflow: hidden;

    &__image {
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }


    &__file {
        position: absolute;
        inset: 0;

        display: flex;
        align-items: center;
        justify-content: center;
        padding: 16px;

        background-color: rgb(53 71 83 / 40%);
        opacity: 0;

        cursor: pointer;
        transition: opacity .3s ease;

        &:hover {
            opacity: 1;
        }

        &_disabled {
            display: none;
        }
    }


    &__input {
        display: none;
    }

    &__text {
        font-size: 18px;
        line-height: 1.2;
        font-weight: 500;

        color: white;
        text-align: center;
		caret-color: transparent;
    }

}
